import React from 'react';
import StudyCategory from './StudyCategory';
import Fade from 'react-reveal/Fade';
import { NavLink } from 'react-router-dom';

function StudyHTML() {
    const items = [
        { to: "/study4_1", title: "HTML" },
        { to: "/study4_2", title: "HTML / " },
        { to: "/study4_3", title: "HTML ()" },
        { to: "/study4_4", title: "CSS " },
        { to: "/study4_5", title: "CSS " },
        { to: "/study4_6", title: "CSS /" },
    ];

    // to     
    items.sort((a, b) => b.to.localeCompare(a.to));

    return (
        <section className="studyConts">
            <div className="inner">
                <StudyCategory />
                <main>
                    <div>
                        <h1>HTML/CSS</h1>
                    </div>
                    <br />
                    {items.map((item, index) => (
                        <Fade key={index} delay={50 * index}>
                            <NavLink to={item.to} className={item.to}>
                                <div className="boardConts">
                                    <div>
                                        <h6>{item.title}</h6>
                                    </div>
                                </div>
                            </NavLink>
                        </Fade>
                    ))}
                </main>
            </div>
        </section>
    );
}

export default StudyHTML;
