import StudyCategory from '../StudyCategory';
import React from 'react';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { prism } from 'react-syntax-highlighter/dist/esm/styles/prism';

function Study2_2() {
    const reactCssImport = 
    `import "./App.css";

    function App() {
        return (
            <div>
                <h2>Hello World!</h2>
            </div>
        );
    }

    export default App;`;

    const reactCssImport2 = 
    `.wrapper {
        width: 80%;
        margin: 0 auto;
    }
    .title {
        width: 100%;
        color: red;
    }
    `;

    const reactCssImport3 = 
    `import "./App.css";

    function App() {
        return (
            <div className="wrapper">
                <h2 className="title">Hello World!</h2>
            </div>
        );
    }

    export default App;`;

    const reactCssImport4 = 
    `// Title.js
    import React from "react";

    export default function Title({ title }) {
        return <h2>{title}</h2>;
    }
    
    ////////////////////////////////////////////////////////////////////////////////////////
    
    // App.js
    import "./App.css";
    import Title from "./Title";

    function App() {
        const TITLE = "Hello Korea";

        return (
            <div className="wrapper">
                <Title title={TITLE} />
            </div>
        );
    }

    export default App;`;

    return(
        <section className="studyConts">
            <div className="inner">
                <StudyCategory />
                <main>
                    <div className="topConts"></div>
                    <h2 className="constTit">, props</h2>
                    <div className='studyContents'>
                        <br/>
                        <h6> </h6>
                        <p>
                            React css   
                        </p>
                        <SyntaxHighlighter language="javascript" style={prism}>
                            {reactCssImport}
                        </SyntaxHighlighter>
                        <SyntaxHighlighter language="css" style={prism}>
                            {reactCssImport2}
                        </SyntaxHighlighter>
                        <br/>
                        <p>className css  .</p>
                        <SyntaxHighlighter language="javascript" style={prism}>
                            {reactCssImport3}
                        </SyntaxHighlighter>
                        <p>export default    import   .   .</p>
                        <br/><br/>

                        <h6>props </h6>
                        <p>
                                    .<br/>
                            props :      data<br/>

                        </p>
                        <SyntaxHighlighter language="javascript" style={prism}>
                            {reactCssImport4}
                        </SyntaxHighlighter>
                        <p>
                            props ,  number, string, boolean      props   .
                        </p>
                        <br />
                        <h5 className='from'>
                             :&nbsp;&nbsp;
                            <span className='fromAddress'>
                                //https://velog.io/@dlsdk0601
                            </span>
                        </h5>
                        
                    </div>
                </main>
            </div>
        </section>
    )
}

export default Study2_2;