import StudyCategory from '../StudyCategory';
import React from 'react';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { prism } from 'react-syntax-highlighter/dist/esm/styles/prism';

function Study2_3() {
    const reactCssImport5 = 
    `import "./App.css";
    import Title from "./Title";

    export default function App() {
        let TITLE = "Hello Korea";

        const clickHandler = () => {
            console.log("");
        };

        return (
            <div className="wrapper">
                <Title title={TITLE} />
                <button onClick={clickHandler}>change Title</button>
            </div>
        );
    }

    export default App;`;

    const reactCssImport6 = 
    `import React, { useState } from "react";
    import "./App.css";
    import Title from "./Title";

    export default function App() {
        const [title, setTitle] = useState("Hello Korea");

        const clickHandler = () => {
            setTitle("one more Hello");
            // setTitle(prev => prev + "plus string");          .
        };

        return (
            <div className="wrapper">
                <Title title={title} />
                <button onClick={clickHandler}>change Title</button>
            </div>
        );
    }

    export default App;`;

    return(
        <section className="studyConts">
            <div className="inner">
                <StudyCategory />
                <main>
                    <div className="topConts"></div>
                    <h2 className="constTit"> useState</h2>
                    <div className='studyContents'>
                        <br/>
                        <h6></h6>
                        <p>
                            HTML    . click, mouseenter, mouseleave ...<br/>
                            react       onClick   .<br/>
                        </p>
                        <SyntaxHighlighter language="javascript" style={prism}>
                            {reactCssImport5}
                        </SyntaxHighlighter>
                        <p>
                             button  console  DOM  .<br/>
                                 DOM  ,       JSX return  .<br/>
                        </p>
                        <br/><br/>
                        <h6>state</h6>
                        <p>
                               ,    useState  .<br/>
                            useState   ,         state      .<br/>
                        </p>
                        <SyntaxHighlighter language="javascript" style={prism}>
                            {reactCssImport6}
                        </SyntaxHighlighter>
                        <p>
                            useState  return .<br/>
                                   /       .<br/>
                            ,       set   <br/>
                             use   hook (useState react hook)<br/>
                            hook   ,    .<br/>
                        </p>
                        <br/>
                        <h5 className='from'>
                             :&nbsp;&nbsp;
                            <span className='fromAddress'>
                                //https://velog.io/@dlsdk0601
                            </span>
                        </h5>
                    </div>
                </main>
            </div>
        </section>
    )
}

export default Study2_3;