import StudyCategory from '../StudyCategory';
import React from 'react';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { prism } from 'react-syntax-highlighter/dist/esm/styles/prism';

function Study2_4() {
    const react7 = 
    `import React, { useState } from "react";
    import "./App.css";
    import Title from "./Title";

    export default function App() {
        const [title, setTitle] = useState("Hello Korea");
        const [text, setText] = useState(""); //       .
        // setText onChange . useState("")    ""  .

        const clickHandler = (e) => {
            //console.log("press");
            setText(e.target.value);
        };

        return (
            <div className="wrapper">
                <Title title={title} />
                <input type="text value={text} onChange={changeHandler} /> 
                //   onChange    .
                <button onClick={clickHandler}>change Title</button>
            </div>
        );
    }

    export default App;`;

    return(
        <section className="studyConts">
            <div className="inner">
                <StudyCategory />
                <main>
                    <div className="topConts"></div>
                    <h2 className="constTit">input state/props state</h2>
                    <div className='studyContents'>
                        <br/>
                        <h6>input state</h6>
                        <p>
                            input           useState  .<br/>
                        </p>
                        <SyntaxHighlighter language="javascript" style={prism}>
                            {react7}
                        </SyntaxHighlighter>
                        <p>
                               ,          !<br/>
                                     .<br/>
                        </p>
                        <br/><br/>
                        <h6>props state</h6>
                        <p>
                            props        data.<br/>
                                 .<br/>
                            ,        props  .<br/>
                              data   .<br/>
                        </p>
                        <br/><br/>
                        <h6>Stateless  / Stateful </h6>
                        <p>
                                  .<br/>
                                       .
                        </p>
                        <br />
                        <h5 className='from'>
                             :&nbsp;&nbsp;
                            <span className='fromAddress'>
                                //https://velog.io/@dlsdk0601
                            </span>
                        </h5>
                    </div>
                </main>
            </div>
        </section>
    )
}

export default Study2_4;